import React from 'react';

export default function CharacterFiltersApplied(props) {
    const {filters} = props;

    return (
        <div>
            {
                Object.keys(filters || {}).map(filterCategory => (
                    (filters[filterCategory] || []).map(filter => filter ? <span>{filter} <i onClick={() => removeFilter()}>x</i></span> : null)
                ))
            }
        </div>
    );
}